
.base-container{

    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    .btnred{
        background-color: red;
    }

    .header{
        font-size: 15px;
        font-family: 'Courier New', Courier, monospace;
        margin: 2rem;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        
        .header1{
           display: flex;
           justify-content: center;
           position: fixed; 
        }
        .tme1{
               width: 100%;
               display: flex;
               justify-content: flex-end;
               align-items: center;
            }
    }
    
    .content{
        display: flex;
        flex-direction: column;
        align-items: center;

        .image{
            width:10em;
            img{
                width: 100%;
                height: 100%;
            }
        }

        .form{
            margin-top: 2em;
            display: flex;
            flex-direction: column;
            align-items: center;

        .form-group{
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            width: fit-content;
            label{
                font-size: 20px;
            }
            input{
                min-width: 18em;
                height: 37px;
                padding: 0px 10px;
                font-size: 16px;
                font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
                background-color: #f3f3f3;
                border: 0px;
                border-radius: 4px;
                transition: all 250ms ease-in-out;
                &:focus{
                    outline: none;
                    box-shadow: 0px 0px 12px 0.8px #0e81ce96;
                }
            }
            }
        }
    }
}